// {"title":"Front Matter","description":"Presenting meta information in Markdown documents.","audience":"Everyone","level":"Beginner","section":"Markdown","link":"/basics/04-front-matter","source":"/home/runner/work/pidoc/pidoc/packages/example-app/docs/basics/04-front-matter.md"}
import * as React from 'react';
import { ContentPage } from '@pidoc/components';

    import { PageContent, Markdown, PageLayout } from '@pidoc/components';

    const link = "https://github.com/smapiot/pidoc/tree/develop/packages/example-app/docs/basics/04-front-matter.md";
    const html = `
<h1 id="markdown-front-matter">Markdown Front Matter</h1>
<p>You can (and should) put so-called front matter at the beginning of each document. For instance, the front matter of this document looks like:</p>
<pre><code class="hljs language-md">---
title: Front Matter
description: Presenting meta information in Markdown documents.
audience: Everyone
level: Beginner
<span class="hljs-section">section: Markdown
---</span>
</code></pre>
<p>Most parts are not really used right now, but you could always add custom parts that show these (e.g., a little indicator next to each documentation page reflecting the level of the content).</p>
<p>In the end, the front matter is processed by the generator. In case of the markdown generator the following parts have special meaning:</p>
<ul>
<li><code>title</code> used as title (i.e., label) in the navigation</li>
<li><code>section</code> used to put the document in the right category within the navigation</li>
</ul>
`;
    const meta = {"title":"Front Matter","description":"Presenting meta information in Markdown documents.","audience":"Everyone","level":"Beginner","section":"Markdown"};
  

export default () => (
  <ContentPage>
    
    <PageLayout name={"default"} meta={meta}>
      <PageContent meta={meta}>
        <Markdown content={html} link={link} editLabel={"Edit on GitHub"} />
      </PageContent>
    </PageLayout>
  
  </ContentPage>
);